<template>
  <div class="w-full">
    <div class="text-center">
      <ul class="flex justify-center gap-x-10 text-lg">
        <li v-for="e in PostCate" :key="e" class="pt-1 px-1 hover:bg-[skyblue] rounded-md hover:text-white ">{{e}}</li>
      </ul>
    </div>
    <div class="max-w-7xl mx-auto my-10 border-b-2 border-indigo-500">
      <ul class="flex justify-between border-t-indigo-500 border-t-2 p-4 py-2 bg-indigo-20 text-xs sm:text-sm text-center">
        <li class="basis-1/12"></li>
        <li class="basis-4/12"></li>
        <li class="basis-2/12"></li>
        <li class="basis-2/12"></li>
        <li class="basis-1/12"></li>
        <li class="basis-1/12"></li>
        <li class="basis-1/12"></li>
      </ul>
      <template v-for="(e, index) in dataList.slice()" :key="index">
        <ul v-if="calculateNumber(totalLength, perPage, page, index) > 0" class="flex justify-between border-b text-center px-4 py-2 even:bg-gray-50 text-xs sm:text-sm">
          <!-- even:   -->
          <li class="basis-1/12 ">{{ calculateNumber(totalLength, perPage, page, index) }}</li>
          <!-- dataList length index    -->
          <li class="basis-4/12 "><router-link :to="{name: 'NoticeRead', query:{docId: dataId[index]}}" 
            @click="$store.commit('NoticeRead', dataId[index])">
            {{ e.title }} 
          </router-link></li>
            <!-- <span readonly class="text-white text-[9px] bg-red-400 rounded-md px-0.5">New</span> -->
          <li class="basis-2/12">{{ e.name }}</li>
          <li class="basis-2/12">{{ BoardDate(index)}}</li>
          <li class="basis-1/12">{{ e.hit }}</li>
          <li class="basis-1/12">{{ e.good }}</li>
          <li class="basis-1/12">{{ e.bad }}</li>
        </ul>
      </template>
    </div>
    <div class="mx-auto text-center">
      <select class="border border-black/20 py-0.5">
        <option v-for="e in writer" :key="e">{{ e }}</option>
      </select>
      <input type="text" placeholder="" class="border border-black/50 ml-1 focus:">
      <button class="py-[1.2px] px-2 ml-0.5 bg-gray-400/50 hover:bg-gray-400"></button>
    </div>
    
    <div v-if="$store.state.loginToken != null" class="flex justify-end pb-24">
      <router-link to="/cs/notice/write" class="bg-indigo-400 hover:bg-indigo-600 focus:ring-indigo-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm -mt-8"></router-link>
    </div>
    <!-- <div class="flex justify-center mx-auto mt-8 mb-14" >
      <ul class="flex flex-nowrap gap-x-5">
        <li class="-mr-1"><img :src="require(`../../assets/images/icon-right-arrow.png`)" alt=" " class="w-[18px] mt-[5px] arrow-left cursor-pointer"></li>
        <li v-for="e in 5" :key="e+1" class="opacity-90 font-light text-lg hover:underline hover:underline-offset-[3px] cursor-pointer">{{ e }}</li>
        <li class="-ml-1"><img :src="require(`../../assets/images/icon-right-arrow.png`)" alt=" " class="w-[18px] mt-[5px] cursor-pointer"></li>
      </ul>
    </div> -->
  </div>
  <div class="flex justify-center basis-full gap-x-10 mb-5">
    <button @click="prevPage" :disabled="currentPage <= 1"></button>
    <button v-for="e in pageCount.pagination" :key="e" @click="goToPage(e)" :class="page == e && 'font-bold text-orange-500'">{{ e }}</button>
    <button @click="nextPage" :disabled="currentPage.totalPage / block"></button>
  </div>
  <!-- {{ block }}      -->
  <!-- {{ perPage }}    -->
  <!-- {{ pageCount.pagination }} -->
  <!-- {{ page }}    -->
  <!-- {{ currentPage }}   -->
</template>
<script>
import {db} from '../../firebase'
import writer from '../../assets/OptionBox.json'
import PostCate from '../../assets/TapCate.json'
export default {
  name:"NoticeList",
  data() {
    return {
      PostCate: PostCate.NoticeCate,
      writer: writer.writer,
      dataList: [],
      dataId: [],
      posts: [],
      page: 1,
      perPage: 5,
      lastVisible: null,
      totalLength: 0,
      block: 5,
      currentPage: 1
    }
  },
  computed:{
    pageCount(){
      const totalPage = Math.ceil(this.totalLength / this.perPage);
      //       /     
      const start = (this.currentPage - 1) * this.perPage
      //       
      const end = start + this.perPage;
      //       
      const pagination = [];
      //     
      for(let i = start + 1; i <= end && i <= totalPage; i++){
        //      ,    .
        //          
        pagination.push(i)
      }
      if(pagination.length === 0 && totalPage > 0){
        pagination.push(totalPage);
      }
      //         0      .
      return {totalPage, start, end, pagination}
    }
  },
  methods:{

    goToPage(e){
      this.page = e;
      //  ( ) 
      this.fetchPost()
      //       
    },
    prevPage(){
      this.currentPage = this.currentPage - 1
      this.page = this.pageCount.pagination[0];
      this.fetchPost();
    },
    nextPage(){
      // this.page ++; ->       1  
      this.currentPage = this.currentPage + 1
      //     1  .
      this.page = this.pageCount.pagination[0];
      //       
      this.fetchPost();
      //     
    },

    BoardDate(index){
      const date = this.dataList[index].date.seconds * 1000 + this.dataList[index].date.nanoseconds / 1000000
      
      const new_date = new Date(date);
      const formattedDate = (''+new_date.getFullYear()).slice(-2) +'-'
      + ('0'+ (new_date.getMonth() + 1)).slice(-2)+'-'
      + ('0'+ new_date.getDate()).slice(-2)+''
      return formattedDate
      },
      
      fetchTotalLength(){
        db.collection("notice").get().then((data)=>{
          //  notice    .
          // size >  (  )
          this.totalLength = data. size
          console.log(data)
        })
      },
      fetchPost(){

        let query = db.collection("notice").orderBy("date", "desc").limit(this.perPage)

        if(this.page > 1 && this.lastVisible){
          query = query.startAfter(this.lastVisible);
          // startAfter   
        }

        query.get().then((data)=>{
          const items = [];
          const ids = [];

          data.forEach((e)=>{
            ids.push(e.id);
            items.push(e.data())
          })

          this.dataId = ids;
          this.dataList = items;
          this.lastVisible = data.docs[data.docs.length - 1]
          // - 1 > 0   
        })
      },
      calculateNumber(totalLength, perPage, page, index){
        const totalPages = perPage * (page - 1);
        //        
        const postIndex = totalLength - (totalPages + index);
        //    -         .
        //     
        if(postIndex <= 0){return}
        //    0    .       0 .

        return postIndex;
        //    >     
      }

  },
  mounted(){
    this.fetchTotalLength();
    this.fetchPost();
  }
}
</script>
<style >
  .arrow-left { transform: scaleX(-1);}
</style>