<template>
  <div class="basis-full">
    <div class="flex justify-center ">
      <ul class="flex justify-center gap-x-7 text-lg">
        <li v-for="e in GalleryCate" :key="e" class="px-1 pb-[1px] pt-1 hover:bg-[skyblue] rounded-md hover:text-white ">{{e}}</li>
      </ul>
    </div>
    <div class="max-w-7xl mx-auto my-10 border-b-2 border-gray-500">
      <ul class="flex justify-between border-t-gray-500/80 border-t-2 p-4 py-2 bg-indigo-20 text-xs sm:text-sm text-center">
        <li class="basis-20"></li>
        <li class="basis-6/12"></li>
        <li class="basis-16"></li>
        <li class="basis-24"></li>
      </ul>
      <ul v-for="(e, index) in dataList.slice()" :key="index" class="flex justify-between border-b text-center px-4 py-2 even:bg-gray-50 text-xs sm:text-sm">
        <li class="basis-20 pr-1">{{ dataList.length - index }}</li>
        <li class="basis-6/12 pl-6"><router-link to="/cs/gallery/read" 
          @click="$store.commit('GalleryRead', dataId[index])">
          {{ e.title }}
        </router-link></li>
        <li class="basis-24 pl-1">{{ e.name }}</li>
        <li class="basis-24 -mr-0.5 shrink-0">{{ BoardDate(dataList.length - (index+1)) }}</li>
      </ul>
    </div>
    <div class="flex justify-end pb-24">
      <router-link to="/cs/gallery/write" class="bg-indigo-400 hover:bg-indigo-600 focus:ring-indigo-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm"></router-link>
    </div>
  </div>
</template>
<script>
import {db} from '../../firebase'
import writer from '../../assets/OptionBox.json'
import GalleryCate from '../../assets/TapCate.json'
export default {
  name:"NoticeList",
  data() {
    return {
      GalleryCate:GalleryCate.GalleryCate,
      writer: writer.writer,
      dataList: [],
      dataId: [],
      posts: [],
      page: 1,
      perPage: 5,
      lastVisible: null,
      totalLength: 0,
      block: 5,
      currentPage: 1
    }
  },
  computed:{
    pageCount(){
      const totalPage = Math.ceil(this.totalLength / this.perPage);
      //       /     
      const start = (this.currentPage - 1) * this.perPage
      //       
      const end = start + this.perPage;
      //       
      const pagination = [];
      //     
      for(let i = start + 1; i <= end && i <= totalPage; i++){
        //      ,    .
        //          
        pagination.push(i)
      }
      if(pagination.length === 0 && totalPage > 0){
        pagination.push(totalPage);
      }
      //         0      .
      return {totalPage, start, end, pagination}
    }
  },
  methods:{

    goToPage(e){
      this.page = e;
      //  ( ) 
      this.fetchPost()
      //       
    },
    prevPage(){
      this.currentPage = this.currentPage - 1
      this.page = this.pageCount.pagination[0];
      this.fetchPost();
    },
    nextPage(){
      // this.page ++; ->       1  
      this.currentPage = this.currentPage + 1
      //     1  .
      this.page = this.pageCount.pagination[0];
      //       
      this.fetchPost();
      //     
    },

    BoardDate(index){
      const date = this.dataList[index].date.seconds * 1000 + this.dataList[index].date.nanoseconds / 1000000
      
      const new_date = new Date(date);
      const formattedDate = (''+new_date.getFullYear()).slice(-2) +'-'
      + ('0'+ (new_date.getMonth() + 1)).slice(-2)+'-'
      + ('0'+ new_date.getDate()).slice(-2)+''
      return formattedDate
      },
      
      fetchTotalLength(){
        db.collection("gallery").get().then((data)=>{
          //  notice    .
          // size >  (  )
          this.totalLength = data. size
          console.log(data)
        })
      },
      fetchPost(){

        let query = db.collection("gallery").orderBy("date", "desc").limit(this.perPage)

        if(this.page > 1 && this.lastVisible){
          query = query.startAfter(this.lastVisible);
          // startAfter   
        }

        query.get().then((data)=>{
          const items = [];
          const ids = [];

          data.forEach((e)=>{
            ids.push(e.id);
            items.push(e.data())
          })

          this.dataId = ids;
          this.dataList = items;
          this.lastVisible = data.docs[data.docs.length - 1]
          // - 1 > 0   
        })
      },
      calculateNumber(totalLength, perPage, page, index){
        const totalPages = perPage * (page - 1);
        //        
        const postIndex = totalLength - (totalPages + index);
        //    -         .
        //     
        if(postIndex <= 0){return}
        //    0    .       0 .

        return postIndex;
        //    >     
      }

  },
  mounted(){
    this.fetchTotalLength();
    this.fetchPost();
  }
}
</script>
