<template>
  <div class="sticky bg-white top-0 w-full border-b border-b-[rgba(255,255,255,0.3)] z-40">
    <!-- border-b-[rgba(255,255,255,0.3)] => bg-b-white/30 -->
    <div class="max-w-7xl mx-auto flex justify-between items-center py-2.5 px-[2%]">
      <div>
        <router-link to="/">
          <img src="https://via.placeholder.com/120x60" alt="">
        </router-link>
      </div>
      <div class="hidden lg:flex justify-between basis-2/3">
        <ul class="flex basis-2/3 justify-between">
          <li @mouseover="NavSelectIndex = index; SubMenu(index)" @mouseout="NavSelectIndex = null" v-for="(e, index) in NavList[0]" :key="e" class="relative basis-1/4 text-center">
            <router-link :to="NavList[1][index]" class="text-base hover:font-bold">{{ e }}<font-awesome-icon class="transition-all duration-500 text-xs align-baseline ml-1" icon="chevron-down" :class="NavSelectIndex === index && RotateClass"/></router-link>
            <ul class="absolute w-36 bg-slate-800 overflow-hidden tansition-all nav_sub duration-500 h-0"
            :style="NavSelectIndex === index && isSubMenu">
              <li class="py-2.5 font-normal" v-for="(el,i) in SubList[index]" :key="el" :class="NavSelectIndex === index && 'font-bold'">
                <router-link :to="SubMenuLink[index][i]" class="text-sm text-white/90 hover:font-bold">{{ el }}</router-link>
              </li>
            </ul>
          </li>
        </ul>
        <ul class="flex gap-x-5">
          <li v-if="$store.state.loginChk == false" class="text-sm">
            <router-link to="/login">
              <FontAwesomeIcon icon="lock"/> 
            </router-link>
          </li>
          <li v-else @click="logOut" class="text-sm">
            <router-link to="/">
              <FontAwesomeIcon icon="arrow-right-from-bracket"/> 
            </router-link>
          </li>
          <li v-if="$store.state.loginChk == true" class="text-sm">
              <router-link to="/memberinfo">  
            </router-link>
         </li>
          <li v-if="$store.state.loginChk == false && 'hidden'" class="text-sm">
            <router-link to="/member">
              <FontAwesomeIcon icon="user" class="pr-1"/>
            </router-link>
          </li>
        </ul>
      </div>
    </div>
    <div class="absolute right-4 md:right-8 top-6 duration-1000 transition-all z-50 cursor-pointer"
      @click="isOpen == true ? isOpen = false : isOpen = true"
      :class="isOpen && 'on'">
      <div v-for="e in 3" :key="e" class="w-[30px] h-0.5 bg-black rounded m-1.5 transition-all duration-1000 lg:hidden">
      </div>
    </div>
    <div class="w-80 h-full fixed bg-gray-50 z-40 -right-80 p-12 box-border transition-all 
    duration-500 top-0 lg:hidden overflow-y-scroll" :class="isOpen && '!right-0'">
    <!-- overflow-y-scroll         -->
    <!-- css  tailwind   0     !   -->
      <ul class="mt-12">
        <li class="py-5 border-b relative cursor-pointer font-bold" v-for="(e, index) in NavList[0]" :key="e" @click="NavSelectIndex = index; SubMenu(index)">{{ e }} <font-awesome-icon icon="chevron-down" class="transition-all duration-500 text-xs align-baseline absolute right-5 top-6" :class="NavSelectIndex === index && RotateClass"/>
          <template v-for="(e,i) in SubList" :key="e">
            <ul v-if="index === i" class="flex flex-wrap justify-between sub_list overflow-hidden transition-all duration-300" 
            style="height: 0px;" :style="NavSelectIndex === index && `height: ${SubHeight}`">
            <!--    h-0  style   -->
              <!--    (li)       style     -->
              <li v-for="(el, i) in e" :key="el" class="basis-full first-of-type:pt-5 pl-5 pt-2 text-xs hover:text-[#37a2d7]">
               <router-link @click="isOpen = false" :to="SubMenuLink[index][i]">{{ el }}</router-link>
                <!--   ,id   -->
                <!-- v-if, v-for    for     , if,for    -->
              </li>
            </ul>
          </template>  
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import {auth} from '../firebase'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
import NavData from '../assets/Data.json'
export default {
    name: "NavCom",
    data() {
        return {
            NavList: NavData.Nav,
            SubList: NavData.SubMenu,
            SubMenuLink: NavData.SubMenuLink,
            NavSelectIndex: null,
            isOpen: false,
            SubHeight: '0px',
            RotateClass: '',
            windowWidth: 0
        };
    },
    methods:{
      logOut(){
        auth.signOut()
        this.$store.commit('logOutToken')
      },

      windowUPdateWidth(){
        this.windowWidth = window.innerWidth;
      },
      SubMenu(e){
      // console.log(e)
        
          const list = document.querySelectorAll(".sub_list")[e];
          const length = list.querySelectorAll("li").length; 
          // console.log(length)
          this.isSubMenu = `height:${length*44}px`;
          // 52 =  li   

            if(list.style.height === '0px'){
              list.style.height = `${length*40}px`;
              this.RotateClass = 'rotate-180'
            }else{
              list.style.height = '0px';
              this.RotateClass = ''
            }
            
            this.SubHeight = list.style.height;
      }
    },
    watch:{ //watch  
      windowWidth(el){ //   width . 
        // console.log(width) - 
        if(el < 1024){
          this.NavSelectIndex = null;
          this.isOpen = false
        }
      }
    },
    mounted(){
      window.addEventListener('resize', this.windowUPdateWidth)
      //       >   ,  ()  
    },
    components: { FontAwesomeIcon }
}
</script>
<style>
  .on div:nth-child(1){
    transform: rotate(45deg) translateY(12px);
  }
  .on div:nth-child(2){
    opacity:0;
    transform: translateX(-30px) rotate(720deg) ;
    /* scale(2) - bar      */
  }
  .on div:nth-child(3){
    transform: rotate(-45deg) translateY(-12px);
  }
</style>