<template>
  <div class="w-full pb-10">
    <div class="max-w-7xl ">
      <p class="mb-2 text-xl pl-1.5 inline-block">{{ BoardContent.title }}</p>
      <p class="pl-1.5 text-right mr-1.5 -mt-4"> : {{ BoardContent.hit }}</p>
      <div class="flex justify-between w-full p-2 border-t border-t-white bg-slate-800/90 text-white">
        <p class=""> : {{ BoardContent.name }}</p>
        <div class="flex justify-end gap-x-5">
          <p @click="PageList()" 
            class="cursor-pointer hover:bg-slate-800 hover:text-white duration-300 transition-all rounded-lg"><router-link to="/cs/notice"><font-awesome-icon icon="bars" class="w-3 mx-1"/></router-link></p>
          <p class="tracking-wider">{{ BoardDate }}</p>
          <!-- <p>{{ BoardDate }}</p> -->
        </div>
      </div>
      
      <div class="w-full rounded-lg md:text-[16px] lg:text-lg pb-10 border-b">
        <p v-html="BoardContent.content" class="p-3  mb-10 text-area"></p>
        <img v-if="BoardContent.file" :src="BoardContent.file" :alt="BoardContent"/>
      </div>
      <div class="flex justify-center text-xl mt-12">
        <font-awesome-icon icon="thumbs-up" title="" class="hover:bg-black hover:text-white rounded-md p-1  cursor-pointer mr-1" @click="GoodChk(0)"/> 
        <p class="mr-6 mt-0.5" >{{ BoardContent.good }}</p>
        <p>{{ dateTime }}</p>
        <font-awesome-icon icon="thumbs-down" title="" class="hover:bg-black hover:text-white rounded-md p-1 text-xl cursor-pointer mt-0.5" @click="GoodChk(1)"/> 
        <p class="mt-0.5 ml-1" >{{ BoardContent.bad }}</p>
      </div>

      <div class="text-right -mt-[30px]">
        <font-awesome-icon icon="share-alt" class="hover:bg-black hover:text-white rounded-md p-1 text-xl cursor-pointer mt-[1px] ml-4" title="SNS "/>
        <font-awesome-icon icon="ban" title="" class="hover:bg-black hover:text-white rounded-md p-1 text-xl cursor-pointer mt-[1px] ml-4"/>
      </div>
      <div class="flex justify-end mt-4">
        <!-- <font-awesome-icon icon="computer" /> -->
        <p @click="PageList()" 
        class="py-1 px-1 mr-3 md:text-[16px] lg:text-lg cursor-pointer hover:bg-slate-800 hover:text-white duration-300 transition-all rounded-lg"><router-link to="/cs/notice"><font-awesome-icon icon="bars" class="w-3 mx-1"/></router-link></p>
        <p @click="goback()" 
        class="py-1 px-1 mr-1 md:text-[16px] lg:text-lg cursor-pointer hover:bg-slate-800 hover:text-white duration-300 transition-all rounded-lg"></p>
      </div>
    </div>
  </div>
  <div class="flex justify-end w-full pb-24 gap-x-5 mt-5">
    <div class="mt-[5px]">
      <router-link to="/cs/notice/list" class="bg-blue-400 hover:bg-blue-600 focus:ring-blue-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm"></router-link>
    </div>
    <div class="gap-x-5 flex">
      <router-link to="/cs/notice/modify" class="bg-green-400 hover:bg-green-600 focus:ring-green-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm"></router-link>

      <button class="bg-red-400 hover:bg-red-600 focus:ring-indigo-400 py-2 px-4 text-white font-semibold rounded-md shadow-md focus:outline-none focus:ring-2 focus:ring-opacity-75 text-xs sm:text-sm"
      @click="Delete"></button>
    </div>
  </div>
</template>
<script>
import {db, auth, storage} from '../../firebase'

export default {
  name:"NoticeRead",
  data() {
    return {
      BoardContent : [],
      dateTime: "",
      file: "",
      BoardDate : null
    }
  },
  mounted(){
    //          
    //        .

    //  if(this.$store.state.noticeId == 0){}   
    //     
    if(this.$route.query.docId == null){
      // id  0     
      // 0   
      this.$router.replace("/cs/notice")
    }
    db.collection("notice").doc(this.$route.query.docId).get().then((data)=>{
      this.BoardContent = data.data()
      
      
    })
    .then(()=>{
      db.collection("notice").doc(this.$route.query.docId).get().then((update)=>{
        this.BoardContent = update.data();
        const milliseconds = this.BoardContent.date.seconds * 1000 + this.BoardContent.nanoseconds / 1000000
        //   date  
        const new_date = new Date(date);
        const formattedDate = (''+new_date.getFullYear()).slice(-2) +'-'
        + ('0'+ (new_date.getMonth() + 1)).slice(-2)+'-'
        + ('0'+ new_date.getDate()).slice(-2)+''
        return formattedDate
      })
      this.BoardContent.hit = this.BoardContent.hit + 1;
      this.BoardContent.good = this.BoardContent.good + 1;
      this.BoardContent.bad = this.BoardContent.bad + 1;
        const date = this.BoardContent.date.seconds * 1000 + this.BoardContent.date.nanoseconds / 1000000
        const new_date = new Date(date);
        const formattedDate = (''+new_date.getFullYear()).slice(-2) +'-'
        + ('0'+ (new_date.getMonth() + 1)).slice(-2)+'-'
        + ('0'+ new_date.getDate()).slice(-2)+''
        // + ('0'+ new_date.getHours()).slice(-2)+':'
        // + ('0'+ new_date.getMinutes()).slice(-2)+':'
        // + ('0'+ new_date.getSeconds()).slice(-2)+''
        this.BoardDate = formattedDate;
    })
    
  },
  methods:{

    GoodChk(e){
      // alert(e)
      const user = auth.currentUser;
      db.collection("users").doc(user.uid).get().then(doc => {
        console.log(doc, e)
        // exists -      
        if(!doc.exists){
          db.collection("users").doc(user.uid).set({
            goodChk: {}
          }).then(()=>{
            this.GoodChk();
          })
        }else{
          const userData = doc.data();
          if(userData.goodChk && userData.goodChk[this.$route.query.docId]){
            alert("    .")
            return;
          }
          const updateField = e === 0 ? 'good' : 'bad';
          const updateData = {};
          updateData[updateField] = this.BoardContent[updateField] + 1;
          //   this.BoardContent['good'] + 1
          //   this.BoardContent['bad'] + 1
          db.collection("notice").doc(this.$route.query.docId).update(updateData).then(()=>{
            //  /       
            userData.goodChk = userData.goodChk || {};
            // userData.GoodChk  ,  userData.goodChk       {}   
            userData.goodChk[this.$route.query.docId] = true
            db.collection("users").doc(user.uid).set(userData).then(()=>{
              db.collection("notice").doc(this.$route.query.docId).get().then((data)=>{
                this.BoardContent = data.data();
              })
            })
          })
        }
      })
    },

    PageList(){
      this.$router.replace("/cs/notice")
    },
    goback(){
      window.history.go(-1);
    },
    Delete(){
      let msg = confirm("    . \r\r ?")
      // confirm   
      if(msg){
        storage.ref().child(`images/${this.FileNameSplit}`).delete()
        db.collection("notice").doc(this.$route.query.docId).delete().then(()=>{
          alert("  .");
          this.$router.replace("/cs/notice")
        })
      }
    },
    hitUpdate(){
      const user = auth.currentUser;
      db.collection("users").doc(user.uid).get().then(doc => {
        // exists -      
        if(!doc.exists){
          db.collection("users").doc(user.uid).set({
            hitChk: {}
          }).then(()=>{
            this.updateHit();
          })
        }else{
          this.updateHit();
        }
      })
        
    },
    updateHit(){
      const user = auth.currentUser;
      db.collection("users").doc(user.uid).get().then(doc => {
        const userData = doc.data();
          if(userData.hitChk && userData.hitChk[this.$route.query.docId]){
            return;
          }
          const updateData = this.BoardContent.hit + 1;
          db.collection("notice").doc(this.$route.query.docId).update({hit: updateData}).then(()=>{
            //  /       
            userData.hitChk = userData.hitChk || {};
            // userData.GoodChk  ,  userData.goodChk       {}   
            userData.hitChk[this.$route.query.docId] = true
            db.collection("users").doc(user.uid).set(userData).then(()=>{
              db.collection("notice").doc(this.$route.query.docId).get().then((data)=>{
                this.BoardContent = data.data();
              })
            })
          })
      })
    },
    
    
    Modify(){
      db.collection("notice").doc(this.$store.state.noticeId).update({
        // update    
        name: this.name,
        title: this.title,
        content: this.content,
        date: this.date,
        file:this.file,
        hit : 0,
        good : 0,
        bad : 0,
        uid: this.$store.state.uid,
        date: this.date
      }).then(()=>{
        alert("  .")
        this.$router.replace("/cs/notice/read")
      })
    }
  },
  computed:{
    FileNameSplit(){
      const parts = this.BoardContent.split("%2F");
      let fileName = "";
      if(this.BoardContent.file){
        fileName = parts[parts.length - 1].split('?')[0]
      }
      return fileName
    }
  }
}
  


</script>
<style >
  .text-area{white-space: pre;}
</style>