import { createStore } from 'vuex'
// vuex        

export default createStore({
  state() { //  data vuex state 
    //       !!
    return{
      number: 0,
      Minus: 10000,
      loginChk: false,
      displayName: "",
      loginToken: null,
      uid: "",
      noticeId: 0,
      onlineId: 0,
      qnaId: 0,
      galleryId:0,
      productId:0,
      kakaoArray: []
    }
  },
  mutations: { // ajax   
    // store    this state 
    //         
    NoticeRead(state, payload){
      // NoticeRead playload   state.noticeId 
      state.noticeId = payload
    },
    OnlineRead(state, payload){
      state.onlineId = payload
    },
    QnaRead(state, payload){
      state.qnaId = payload
    },
    GalleryRead(state, payload){
      state.galleryId = payload
    },
    NumberPlus(state){
      state.number++
    },
    NumberMinus(state){
      state.Minus--
    },
    loginToken(state, payload){
      state.loginToken = payload.refreshToken
      state.loginChk = true
      state.uid = payload.uid
      console.log(payload.uid)
    },
    logOutToken(state){
      state.loginChk = false
      state.loginToken = null
      state.displayName = ''
      state.uid = ''
      sessionStorage.removeItem("refreshToken")
      sessionStorage.removeItem("displayName")
    },
    loginState(state, payload){
      // localstorage -      
      // sessionstorage -    
      state.loginToken = sessionStorage.getItem("refreshToken");
      state.displayName = payload.displayName
      state.uid = payload.uid
      state.loginChk = true
    },
    kakaoLogin(state, payload){
      state.kakaoArray = payload,
      state.loginToken = payload.res.data.access_token
      state.loginChk = true
    }
  },
  actions: {
  },
  
})
